import { Component, ElementRef, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { trigger, state, style, animate, transition } from '@angular/animations';

/**
 * Generated class for the ChangeSofDetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-change-sof-detail',
  templateUrl: 'change-sof-detail.html',
  animations: [
    trigger('fadeInOut', [
      transition(':enter', [
        style({ opacity: 0 }),
        animate('200ms ease-in', style({ opacity: 1 }))
      ]),
      transition(':leave', [
        animate('200ms ease-out', style({ opacity: 0 }))
      ])
    ]),
    trigger('slideUpDown', [
      transition(':enter', [
        style({ transform: 'translateY(100%)' }),
        animate('250ms ease-out', style({ transform: 'translateY(0)' }))
      ]),
      transition(':leave', [
        animate('250ms ease-in', style({ transform: 'translateY(100%)' }))
      ])
    ])
  ]
})
export class ChangeSofDetailPage {
  bgCekCC = "assets/imgs/gen-z/bliv-ver-temp5.png";
  isLottie = false;
  isLoading: boolean = false;
  isShowModalDetailCC: boolean = false;
  isShowModalMenuCC: boolean = false;
  listMenu = [{
    title: 'Bayar',
    img: 'assets/imgs/gen-z/menu-bayar.png'
  },
  {
    title: 'Ubah Cicilan',
    img: 'assets/imgs/gen-z/menu-bayar.png'
  },
  {
    title: 'Mega Bill',
    img: 'assets/imgs/gen-z/menu-bayar.png'
  },
  {
    title: 'Credit Shield',
    img: 'assets/imgs/gen-z/menu-bayar.png'
  }];

  listMenuGear = [{
    title: 'Pengaturan Limit',
    img: 'assets/imgs/gen-z/atur-limit.png'
  },
  {
    title: 'Blokir & Ganti Kartu',
    img: 'assets/imgs/gen-z/blokir-kartu.png'
  },
  {
    title: 'Ubah PIN Kartu Kredit',
    img: 'assets/imgs/gen-z/ubah-pin.png'
  },
  {
    title: 'Registrasi Kartu Tambahan',
    img: 'assets/imgs/gen-z/regis-supplement.png'
  },
  {
    title: 'Atur Jenis Transaksi',
    img: 'assets/imgs/gen-z/atur-transaksi.png'
  },
  {
    title: 'Loan On Card',
    img: 'assets/imgs/gen-z/loc.png'
  }];

  animatedValue: number = 0;

  lottieConfig: any;
  anim: any;

  constructor(public navCtrl: NavController, public navParams: NavParams) {
    this.lottieConfig = {
      path: 'assets/file/cat-lottie.json',
      renderer: 'canvas',
      autoplay: true,
      loop: true
    }
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ChangeSofDetailPage');
  }

  showModalDetailCC(val) {
    this.isShowModalDetailCC = val;
  }

  showModalMenuCC(val) {
    this.isShowModalMenuCC = val;
  }

  handleAnimation(anim: any) {
    console.log('anim', anim)
    this.anim = anim;
    this.anim.setSpeed(1);
  }

  setLottie() {
    this.isLottie = !this.isLottie;
  }


}



